import {useState, useEffect, useRef, useMemo} from 'react';
import matchSorter from "match-sorter";

export default function useSelect({
  multi,
  options,
  value,
  onChange,
  scrollToIndex,
  shiftAmount = 5,
  optionsRef: userOptionsRef
})
  {

  if (multi && !Array.isArray(value)){
    console.warn(`useSelect value must be an array`);
  }

  let selectedOption = useMemo(
    () => {
      if (!multi) {
        return options.find(d => d.value === value);
      } else {
        return value.map(val => options.find(d => d.value === val));
      };
    },
    [value]
  );

  const [searchValue, setSearchValue] = useState("");
  const [isOpen, setIsOpen] = useState(false);
  const [highlightedIndex, _setHighlightedIndex] = useState(0);

  options = searchValue
    ? matchSorter(options, searchValue, { keys: ["label"] })
    : options;

  const highlightedOption = options[highlightedIndex]

  useClickOutsideRef(
    isOpen,
    () => {
      setIsOpen(false);
    },
    userOptionsRef
  );

  const setHighlightedIndex = updater =>
    _setHighlightedIndex(old =>
      Math.min(Math.max(0, updater(old)), options.length - 1)
    );

  const selectOption = option => {
    if (!multi){
      onChange(option.value);
      setSearchValue("");
    } else if (!value.includes(option.value)) {
      onChange([...value, option.value], option.value);
    }
    if (!multi){
      setIsOpen(false);
    } else {
      setSearchValue("");
    }
  };

  const removeValue = newValue => {
    onChange(value.filter(d => d !== newValue));
  };

  const handleSearchValueChange = e =>{
    setSearchValue(e.target.value);
    setIsOpen(true);
  };

  const handleSearchClick = () => {
    setSearchValue("");
    setIsOpen(true);
  };
  const handleSearchFocus = () => handleSearchClick();

  const highlightOption = option => {
    setHighlightedIndex(old => {
      const index = options.indexOf(option);
      if (index > -1){
        return index;
      }
      return old;
    });
  };

  const getKeyProps = useKeys({
    ArrowUp: ({ shift }, e) => {
      e.preventDefault();
      const amount = shift ? shiftAmount - 1 : 1;
      setIsOpen(true);
      setHighlightedIndex(old => old - amount);
    },
    ArrowDown: ({ shift }, e) => {
      e.preventDefault();
      const amount = shift ? shiftAmount - 1 : 1;
      setIsOpen(true);
      setHighlightedIndex(old => old + amount);
    },
    Enter: () => {
      selectOption(highlightedOption);
    },
    Escape: () => {
      setIsOpen(false);
    },
    Tab: () => {
      setIsOpen(false);
    }
  });

  const getInputProps = ({onChange, onFocus, onClick} = {}) =>{
    return getKeyProps({
      value: isOpen || !selectedOption ? searchValue : selectedOption.label,
        onChange: e =>{
          handleSearchValueChange(e);
          if (onChange) {
            onChange(e);
          }
        },
        onFocus: e =>{
          if (onFocus) {
            onFocus(e);
          }
        },
        onClick: e => {
          if (onClick){
            onClick(e)
            setSearchValue("")
          }
        }
    });
  };

  const getOptionProps =({ option, onClick, onMouseEnter, ...rest } = {} ) =>{
    if (!option){
      throw new Error(
        `'useSelect.getOptionProps' requires an option prop, eg 'getOptionProps({option.map})'`
      );
    }
    return {
      ...rest,
      onClick: e => {
        selectOption(option);
        if (onClick) {
          onClick(e);
        }
      },
      onMouseEnter: e => {
        highlightOption(option)
        if (onMouseEnter) {
          onMouseEnter(e);
        }
      }
    };
  };

  useEffect(
    () => {
      setHighlightedIndex(() => 0);
    },
    [searchValue]
  );

  useEffect(
    () => {
      setHighlightedIndex(() => 0);
    },
    [isOpen]
  );

  useEffect(
    () => {
      scrollToIndex(highlightedIndex);
    },
    [highlightedIndex]
  );

  useEffect(
    () => {
      if (selectedOption){
        setSearchValue(selectedOption.value);
      }
    },
    [selectedOption]
  );

  return{
    visibleOptions: options,
    selectedOption,
    highlightedOption,
    selectOption,
    getInputProps,
    isOpen,
    searchValue,
    highlightOption,
    getOptionProps
  };
}

function useClickOutsideRef(enable, fn, userRef) {
  const localRef = useRef();
  const elRef = userRef || localRef;

  const handle = e => {
    const isTouch = e.type === "touchstart";
    if (e.type === "click" && isTouch){
      return;
    }
    const el = elRef.current;
    if (el && !el.contains(e.target)) fn(e);
  };

  useEffect(() =>{
    if (enable) {
      document.addEventListener("touchstart", handle, true);
      document.addEventListener("click", handle, true);
    }

    return () => {
      document.addEventListener("touchstart", handle, true);
      document.addEventListener("click", handle, true);
    };
  });
}

const useKeys = userKeys => {
  return({ onKeyDown, ...rest } = {}) => {
    return{
      ...rest,
      tabIndex: "1",
      onKeyDown: e => {
        e.persist();
        const {keyCode, key, shiftKey: shift } = e;
        const handler = userKeys[key] || userKeys[keyCode];
        if (handler) {
          handler(
            {
              keyCode,
              key,
              shift
            },
              e
          );
        }
        if (onKeyDown) {
          onKeyDown(e);
        }
      }
    };
  };
};
