import React, {useState} from 'react';
import './navigation.css'

export default function Nav(){
  const [opened, setOpen] = useState(false);

  function Open(){
    return(
      <div className = "OpenNavigation">
        <div className = "Exit" onClick={() => setOpen(opened => !opened)}>X</div>
          <div  className = "MenuItem1"> User </div>
          <div  className = "MenuItem2"> Search </div>
          <div  className = "MenuItem3"> Saved </div>
          <div  className = "MenuItem4"> Quiz </div>
          <div  className = "MenuItem5"> Explore </div>
      </div>
    )
  };

  function Hamburger() {
    return (
      <div className = "Hamburger" onClick = {() => setOpen(opened => !opened)}>
        <div> </div>
        <div> </div>
        <div> </div>
      </div>
    )
  };

  if (opened) {
  return(
    <Open/>
   )
 };
  return(
    <div className = "Navigation">
      <h2 className = "logo">Adiona</h2>
      <Hamburger/>
    </div>
  )
}
