import React, { useRef } from "react";
import useSelect from "./useSelect";
import styled from "styled-components";
import matchSorter from "match-sorter";
import { FixedSizeList } from "react-window";

const OptionsWrapper = styled.div`
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1;
  color: black;
`;

const Options = styled(FixedSizeList)`
  border: 1px solid black;
`;

const Option = styled.div`
  background: ${props =>
    props.highlighted ? "lightblue" : props.selected ? "lightgray" : "white"};
  display: flex;
  alignitems: center;
  padding: 0.5rem;
`;


export default function LocationSelector({
  multi,
  value,
  options,
  onChange,
  visibleItems = 10,
  itemHeight = 40
}) {
  const reactWindowInstanceRef = useRef();
  const optionsRef = useRef();

  const scrollToIndex = index => {
    if (!reactWindowInstanceRef.current) {
      return;
    }
    reactWindowInstanceRef.current.scrollToItem(index);
  };


  const shiftAmount = visibleItems;
  const {
    visibleOptions,
    selectedOption,
    highlightedOption,
    getInputProps,
    getOptionProps,
    isOpen
  } = useSelect({
    multi,
    options,
    value,
    onChange,
    scrollToIndex,
    optionsRef,
    shiftAmount,
  });

  const height = Math.min(visibleItems, visibleOptions.length) * itemHeight;

  return (
    <div style={{ display: "inline-block", position: "relative" }}>
      <input {...getInputProps()}  placeholder="Choose the places you'd like to visit..."/>
      <OptionsWrapper ref={optionsRef} >
        {isOpen ? (
          <Options
            ref={reactWindowInstanceRef}
            height={height}
            itemCount={visibleOptions.length || 1}
            itemSize={itemHeight}
            width={400}
          >
            {React.forwardRef(({ index, style, ...rest }, ref) => {
              const option = visibleOptions[index];
              if (!visibleOptions.length) {
                return (
                  <Option ref={ref} style={style}>
                    No Results found
                  </Option>
                );
              };
              return (
                <Option
                  {...getOptionProps({
                    option,
                    ref,
                    style,
                    highlighted: option === highlightedOption,
                    selected: option === selectedOption
                  })}
                >
                  {option.label}
                </Option>
              );
            })}
          </Options>
        ) : null}
      </OptionsWrapper>
      {multi ? (
        <div>
          {selectedOption.map(option => (
            <div key={option.value}>
              {option.value}{" "}
              <span class = "remove"
                onClick={() => onChange(value.filter(d => d !== option.value))}
              >
                x
              </span>
            </div>
          ))}
        </div>
      ) : null}
    </div>
  );
}
